import React from "react";
import { Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import { useSelector } from "react-redux";
import { IState } from "../types";

export const Navbar: React.FC = () => {
  const store = useSelector((data: IState) => data);

  const toggler: any = (param: string) => {
    return store[param] ? "navbar-link active" : "navbar-link";
  };

  const { t } = useTranslation();
  return (
    <div className="container-fluid my-navbar">
      <div className="container my-navbar-inside">
        <div className="left-side">
          {" "}
          <Link className={toggler("showcase")} to="/">
            {t("Home")}
          </Link>
          <Link className={toggler("about")} to="/about">
            {t("About")}
          </Link>
          <Link className={toggler("work")} to="/work">
            {t("Work")}
          </Link>
        </div>
        <Link className={toggler("contact")} to="/contact">
          {t("Contact")}
        </Link>
      </div>
    </div>
  );
};
