import { IAction } from "../types";
import {
  aboutToggle,
  contactToggle,
  showcaseToggle,
  workToggle,
} from "./redux-type";

export const showcase = (payload: boolean): IAction => {
  return {
    type: showcaseToggle,
    payload,
  };
};

export const about = (payload: boolean): IAction => {
  return {
    type: aboutToggle,
    payload,
  };
};

export const contact = (payload: boolean): IAction => {
  return {
    type: contactToggle,
    payload,
  };
};

export const work = (payload: boolean): IAction => {
  return {
    type: workToggle,
    payload,
  };
};
