import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { contact } from "../redux/actions";

export const Contact: React.FC = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(contact(true));
  }, []);

  useEffect(() => {
    return () => {
      dispatch(contact(false));
    };
  });

  const { t } = useTranslation();
  return (
    <div className="contact-page">
      <div className="container mt-5 contact-page-inside">
        <h1 className="title">Contact</h1>
        <br />
        <br />
        <br />
        <div className="numbers">
          <div>
            <i className="fas fa-phone"></i>
            <a href="tel:+998949347204">{t("Phone: +998949347204")}</a>
          </div>

          <div>
            <i className="fab fa-telegram"></i>
            <a href="https://t.me/ibrohim_abdulfattah">
              {t("Telegram: @ibrohim_abdulfattah")}
            </a>
          </div>

          <div>
            <i className="fas fa-envelope"></i>
            <a href="mailto:ibrohim97uz@gmail.com">
              {t("Phone: Ibrohim97uz@gmail.com")}
            </a>
          </div>

          <div>
            <i className="fab fa-instagram"></i>
            <a href="https://www.instagram.com/ibrohim_abdulfattah/">
              {t("Instagram: Ibrohim Abdulfattah")}
            </a>
          </div>
        </div>
      </div>
    </div>
  );
};
