import { ReducerState } from "react-router/node_modules/@types/react";
import { IAction, IState } from "../types";
import {
  aboutToggle,
  contactToggle,
  showcaseToggle,
  workToggle,
} from "./redux-type";

const InitialState: IState = {
  showcase: false,
  about: false,
  contact: false,
  work: false,
};

export const RootReducer = (state: IState = InitialState, action: IAction) => {
  switch (action.type) {
    case showcaseToggle:
      return (state = { ...InitialState, showcase: action.payload });
    case aboutToggle:
      return (state = { ...InitialState, about: true });
    case workToggle:
      return (state = { ...InitialState, work: true });
    case contactToggle:
      return (state = { ...InitialState, contact: true });
    default:
      return state;
  }
};
