import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { showcase } from "../redux/actions";

export const Showcase: React.FC = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(showcase(true));
  }, []);

  useEffect(() => {
    return () => {
      dispatch(showcase(false));
    };
  });
  const { t } = useTranslation();

  return (
    <div>
      <div className="showcase">
        <div className="container showcase-inside">
          <div className="showcase-text">
            <div>
              <br />
              <br />
              <h4>{t("Hi, my name is Ibrohim")}</h4>
              <br />
              <h1>I'm Frontend developer</h1>
              <br />
              <p>
                I have been doing professional website development for 1 years
                already. Over the years of his activity he has created websites
                of different complexity and types.
              </p>
              <Link className="active learn-more" to="/about">
                Learn more <span className="span-animate">&gt;</span>
              </Link>
            </div>
          </div>
          <div className="hero"></div>
        </div>
      </div>
      <h1 className="title">Videos</h1>
      <div className="video">
        <div className="video-wrapper">
          <iframe
            src="https://www.youtube.com/embed/h4NQYbWnO-c"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            title="YouTube video player"
            frameBorder="0"
            allowFullScreen
          ></iframe>
        </div>
      </div>
    </div>
  );
};
