import React, { useEffect } from "react";
import { useTranslation } from "react-i18next";
import { useDispatch } from "react-redux";
import { about } from "../redux/actions";

export const About: React.FC = () => {
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(about(true));
  }, []);

  useEffect(() => {
    return () => {
      dispatch(about(false));
    };
  });
  const { t } = useTranslation();
  return (
    <div className="about-page">
      <div className="container mt-5">
        <h1 className="title">{t("About me")}</h1>
        <br />
        <br />
        <p>
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Harum
          necessitatibus id optio nisi ullam modi mollitia iste omnis, sint odio
          veniam quia quasi quae molestiae cupiditate error facilis voluptatum,
          libero expedita, totam hic repudiandae adipisci ex. Omnis error enim
          asperiores ipsa tempore amet sit iusto inventore in nemo, expedita
          voluptates nesciunt consectetur odit officiis ipsam maxime quos
          veniam. Qui, cupiditate ullam voluptatem perferendis vel autem, ad
          fugit, eveniet explicabo neque aliquam reiciendis aliquid facilis!
          Sunt similique ratione, dolore necessitatibus commodi quasi magnam aut
          unde nemo minima aperiam facere quam repellat itaque esse reiciendis
          quos sequi. Odio, repudiandae accusantium? Ex, provident.
        </p>
      </div>
    </div>
  );
};
